
import { defineComponent, onMounted, ref } from 'vue';

//@ts-ignore
import Prism from 'prismjs';

export default defineComponent({
  name: 'DemoBlock',
  setup() {
    onMounted(() => {
      Prism.highlightAll();
    });

    const hovering = ref(false);
    const isExpanded = ref(false);
    const fixedControl = ref(false);

    return {
      hovering,
      isExpanded,
      fixedControl,
    };
  },
});
